import React, { useState } from "react";
import { Link } from "react-scroll";
import Navigation from "../Navigation/Navigation";
import { HeaderHero } from "./LandingPageHeader.styles";
import { useQuery } from "@apollo/react-hooks";
import { GetLandingPageQuery } from "../../data/ApiService";
import { AboutSectionWrapper } from "../AboutSection/AboutSection.styles";
import {
  HeaderHeroWrapper,
  HeaderTitle,
  HeaderNavigation,
  HeaderParallaxBg
} from "./LandingPageParallax.styles";
import NavigationLongTitles from "../Navigation/NavigationLongTitles";

const LandingPageParallax = () => {
  const [isMobileActive, setIsMobileActive] = useState(false);
  const { loading, error, data } = useQuery(GetLandingPageQuery);
  const dataHtml = data ? data.staticContent.content.text : "";
  const htmlDecode = input => {
    var e = document.createElement("div");
    e.innerHTML = input;
    return e.childNodes.length === 0 ? "" : e.childNodes[0].nodeValue;
  };
  return (
    <HeaderHeroWrapper>
      <HeaderNavigation>
        <div
          className="fixed-mobile-nav-trigger"
          onClick={() => {
            setIsMobileActive(true);
          }}
        >
          <i className="fa fa-bars"></i>
        </div>
        <div
          className={`fixed-mobile-nav-trigger close-it  ${
            isMobileActive ? "active" : ""
          }`}
          onClick={() => {
            setIsMobileActive(false);
          }}
        >
          <i className="fa fa-times"></i>
        </div>
        <div className={`mobileNavigation ${isMobileActive ? "active" : ""}`}>
          <NavigationLongTitles
            location="headerNav"
            onLinkClicked={() => {
              setIsMobileActive(false);
            }}
          />
        </div>
        <NavigationLongTitles location="headerNav" onLinkClicked={() => {}} />
      </HeaderNavigation>
      <HeaderTitle>
        <div className="headerTitle">
          <h1> Jessica Sutherland </h1>
          <span>
            <div dangerouslySetInnerHTML={{ __html: dataHtml }} />
          </span>
          <div className="actionButtons">
            <Link
              className="titleButton"
              to="aboutSection"
              spy={true}
              smooth={true}
              duration={1500}
              onClick={() => {}}
            >
              Learn More
            </Link>
            <Link
              className="noBgButton"
              to="contactSection"
              spy={true}
              smooth={true}
              duration={500}
              onClick={() => {}}
            >
              Let's Talk
            </Link>
          </div>
        </div>
      </HeaderTitle>
      <HeaderParallaxBg>
        <div
          className="imageParallax imageTopLeft"
          style={{ backgroundImage: "url(bg-shape-top-left.png)" }}
        ></div>
        <div
          className="imageParallax imageBottomRight"
          style={{ backgroundImage: "url(bg-shape-bottom-right.png)" }}
        ></div>
        <div
          className="imageParallax imagePerson"
          style={{ backgroundImage: "url(bg-woman2.png)" }}
        ></div>
      </HeaderParallaxBg>
    </HeaderHeroWrapper>
  );
};

export default LandingPageParallax;
