import styled from "styled-components";

const popColor1 = "#e3b923";
export const HeaderHeroWrapper = styled.header`
  display: flex;
  position: relative;
  overflow: hidden;
  height: 100vh;
  @media only screen and (max-width: 1200px) {
    max-height:800px;
  }
  .actionButtons {
    margin-top: 50px;
    margin-left:10px;
  }
  .noBgButton{
    font-weight:500;
    cursor:pointer;
    &:hover,
    &.active {
      border-bottom: 2px solid ${popColor1};
      padding-bottom: 5px;
    }
  }
  .titleButton {
    display: inline-block;
    padding: 10px 12px;
    margin-right:50px;
    background: ${popColor1};
    border-radius: 6px;
    font-size: 16px;
    font-weight: bold;
    color: white !important;
    cursor: pointer;
    &:hover {
      background-color: #444;
      // color: ${popColor1} !important;
    }
    a {
      color: white;
    }
  }

`;
export const HeaderNavigation = styled.div`
  display: flex;
  align-items: flex-end;
  flex-direction: row;
  padding: 0.5rem 1rem;
  position: absolute;
  top: 0;
  padding: 50px 10%;
  width: 100%;
  .headerNav {
    width: 100%;
    margin: 0;
    align-self: flex-end;
    text-align: right;
    a {
      text-transform: none;
      font-size: 14px;
      font-weight: 400;
      margin: 15px;
    }
  }
  .mobileNavigation {
    display: none;
    opacity: 0;
    transition: all 500ms;
    // display: none;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 101;
    height: 100vh;
    width: 100%;
    background: #333;
    align-items: center;
    text-align: center;
    justify-content: center;
    padding: 30px;
    color: white;
    flex-direction: column;
    .headerNav {
      text-align: center;
      display: flex;
      flex-direction: column;
      margin: 0;
      align-self: center;
      li {
        a {
          color: white;
        }
        line-height: 1;
      }
      li:before {
        display: none;
      }
    }
  }
  .fixed-mobile-nav-trigger {
    display: none;
    z-index: 100;
    font-size: 20px;
    cursor: pointer;
    color: white;
    position: fixed;
    top: 20px;
    left: 20px;
    justify-content: center;
    align-items: center;
    background-color: ${popColor1};
    height: 40px;
    width: 40px;
    transition: all 500ms;
    border-radius: 100px;
    &.close-it {
      display: none;
      z-index: 102;
      left: auto;
      right: 20px;
      &.active {
        display: flex;
      }
    }
  }
  @media only screen and (max-width: 980px) {
    background: #fff;
    .fixed-mobile-nav-trigger {
      display: flex;
    }
    .mobileNavigation.active {
      display: flex;
      opacity: 1;
    }
  }
`;
export const HeaderTitle = styled.div`
  align-items: center !important;
  align-self: center !important;
  width: 100%;
  margin: 0 10%;
  margin-top:80px;
  padding: 0 15px;
  max-width: 1140px;
  .headerTitle {
    top:-100px;
  }
  h1 {
    font-family: "Dianna Webber", cursive;
    color:${popColor1}
    font-size: 6.3vw;
    margin-bottom:0px;
  }
  span {
    display:block;
    width: 51%;
    align-self: center;
    font-size: 16px;
    line-height: 1.5;
    margin: 0;
    padding: 0 0 0 15px;
  }
  @media only screen and (max-width: 980px) {
    margin:0;
    .headerTitle{
      margin-top:0;
      text-align:center;
    }
    h1{
      font-size:15vw !important;
    }
    span{
      width:100%
    }
  }
`;
export const HeaderParallaxBg = styled.div`
  bottom: 0;
  left: 0;
  height: 100%;
  overflow: hidden;
  position: absolute;
  width: 100%;
  z-index: -2;
  .imageParallax {
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    left: 0%;
    top: 0%;
    height: 100%;
    overflow: hidden;
    width: 100%;
    position: absolute;
  }
  .imageBottomRight {
    bottom: 0%;
    top: auto;
    z-index: -1 !important;
    display: none;
  }
  .imagePerson {
    bottom: 0%;
    top: auto;
    z-index: 1 !important;
    // background-size: contain;
    background-position: bottom;
  }
  @media only screen and (max-width: 980px) {
    .imageParallax {
      display: none;
    }
  }
`;
