import { Link } from "react-scroll";
import React from "react";
import { NavigationWrapper } from "./Navigation.styles";

const NavigationLongTitles = props => {
  const { location, onLinkClicked } = props;

  return (
    <NavigationWrapper className={location && location}>
      <li>
        <Link
          to="aboutSection"
          spy={true}
          smooth={true}
          duration={500}
          onClick={() => {
            onLinkClicked();
          }}
        >
          Learn About Me
        </Link>
      </li>
      <li>
        <Link
          to="portfolioSection"
          spy={true}
          smooth={true}
          duration={500}
          onClick={() => {
            onLinkClicked();
          }}
        >
          View My Work
        </Link>
      </li>
      <li>
        <Link
          to="contactSection"
          spy={true}
          smooth={true}
          duration={500}
          onClick={() => {
            onLinkClicked();
          }}
        >
          Let's Connect
        </Link>
      </li>
      <li>
        <a href="/JessicaSutherlandResume.pdf" target="_blank">
          My Resume
        </a>
      </li>
    </NavigationWrapper>
  );
};

export default NavigationLongTitles;
